import { Component, OnInit  } from '@angular/core';
import * as moment from 'moment';
import * as datajson from './assets/data.json';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit  {
  
  showblog: boolean = false;
  currentyear = moment(new Date()).format('YYYY');
  dobage = parseInt(moment(new Date()).format('YYYY')) - 1993;
  address = "Boralesgamuwa, Sri Lanka";
  mythought: String = ` I’ve always been a great problem solver, and a technophile obsessed with the latest devices.
  Today, I’m working from home as a software engineer for <b>BoardPAC (Pvt) Ltd</b>, and I am getting into <b>Amazon Web Services</b> on my own time.
  <br></br>
  Familiar with a variety of programming languages, including JavaScript, Python and React, but I’m always adding new skills to my repertoire. I’m also eager to meet other software engineers in the area, so feel free to connect!`;
  title = 'my-cv';
  myworkexpreice = datajson.myworkexpreice;
  myEducation = datajson.myEducation;
  myBlog = datajson.myBlog;
  blogHeader: string = "";
  blogbody: any;
  blogDate: any;
  order:string="";
  constructor() {


  }

  showorigin() { 
    this.showblog = false;
  }

  showblogItem() { 
    this.blogHeader = this.myBlog[0].header;
    this.blogbody = this.myBlog[0].body;
    this.blogDate = this.myBlog[0].date;
    this.blogDate = this.myBlog[0].order;
    this.showblog = !this.showblog;
    window.scrollTo(0, 0);
  }
  showblogfun(item) {
    this.blogHeader = item.header;
    this.blogbody = item.body;
    this.blogDate = item.date;
    this.order = item.order;
    this.showblog = !this.showblog;
    window.scrollTo(0, 0);
  }
  whatiknow(item) { 
    this.blogHeader = item.header;
    this.blogbody = item.body;
    this.blogDate = item.date;  
    this.order = item.order;
    window.scrollTo(0, 0);
  }

  ngOnInit() {

  }


}

